.posts {
  padding: 80px 0;
  .slider {
    display: flex;
    margin-top: 40px;
    .slick-arrow {
      top: 110%;
      left: 46%;
      &.slick-next {
        margin-left: 30px;
        &::before {
          content: url('../img/next.png');
        }
        ;
      }
      &.slick-prev {
        &::before {
          content: url('../img/last.png');
        }
        ;
      }
    }
    .item {
      margin: 15px;
      &:hover {
        .overlay {
          opacity: .7;
        }
        .data {
          opacity: 1;
          .num {
            background: #c7b299;
          }
          .sep {
            background: #a48d72;
          }
          color: #fff;
        }
        .text {
          background: #362f2d;
          h6 {
            color: #c7b299;
          }
          p {
            color: #fff;
            max-width: 300px;
          }
        }
      }
      a {
        display: block;
        position: relative;
        .overlay {
          background: #3b3b3a;
          width: 100%;
          position: absolute;
          height: 100%;
          transition: opacity 2s;
          opacity: 0;
        }
        .data {
          transition: opacity 2s;
          opacity: 1;
          position: absolute;
          top: 8%;
          left: 5%;
          color: #fff;
          .num {
            font-family: 'Montserrat';
            background: #363636;
            font-size: 24px;
            padding: 5px 10px;
          }
          .sep {
            font-size: 12px;
            background: #252525;
            padding: 5px 15px;
            text-transform: capitalize;
          }
        }
      }
      .text {
        transition: opacity 2s;
        padding: 20px 0;
        background: #fbfaf8;
        padding-left: 22px;
        h6 {
          color: #555;
          font-size: 20px;
          font-weight: 500;
          font-family: 'Raleway';
        }
        p {
          font-size: 14px;
          color: #d1d1d1;
        }
        .btn {
          background: none;
          color: #c7b299;
          font-size: 14px;
          border: none;
          padding: 0;
          margin-top: 30px;
        }
      }
    }
  }
}