.video{
    height: 450px;
    position: relative;
    .md(height,350px);
    video{
        width: 100%;
        height: 100%;
        object-fit: cover;
        display: block;
    }
    .item{
        position: absolute;
        top: 40%;
        .lg(top, 30%);
        .md(top,15%);
      width: 100%;
        text-align: center;
        img{
            cursor: pointer;
        }
        h3{
            font-weight: 500;
            color: #fff;
            font-size: 30px;
        }
        p{
            font-size: 16px;
            color: #fff;
            max-width: 560px;
            margin: auto;
        }
    }
}