.projects {
  padding: 70px 0;
  h3 {
    font-size: 30px;
  }
  p {
    font-size: 16px;
  }
  .nav {
    margin: 35px 0;
    justify-content: center;
    li {
      margin: 5px;
      .md(margin, 10px);
      &.active {
        a {
          background: #998675;
          color: #fff !important;
        }
      }
      a {
        border: 1px solid #ebebeb;
        padding: 8px 16px;
        border-radius: 5px;
        background: #fff;
        color: #8c8c8c;
        text-decoration: none;
      }
    }
  }
  .tab-content {
    .tab-pane {
      .tabs {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .item {
          margin: 15px;
          .xl(margin, 10px);
          flex-basis: 350px;
          .xl(flex-basis, 290px);
          .md(flex-basis, 250px);
          &:nth-child(1) {
            margin-left: 0;
            .xl(margin-left, 10px);
          }
          &:nth-child(3) {
            margin-right: 0;
            .xl(margin-right, 10px);
          }
          &:hover {
            .overlay {
              opacity: .8;
            }
            .link-icon {
              opacity: 1;
            }
            .text {
              background: #362f2d;
              h6 {
                color: #fff;
              }
              p {
                color: #c7b299;
              }
            }
          }
          a {
            display: block;
            position: relative;
            .overlay {
              background: #3b3b3a;
              width: 100%;
              position: absolute;
              height: 100%;
              transition: opacity 2s;
              opacity: 0;
            }
            .link-icon {
              transition: opacity 2s;
              opacity: 0;
              position: absolute;
              top: 40%;
              left: 40%;
              .xl(left, 34%);
              font-size: 26px;
              .fa {
                color: #fff;
                margin: 0 5px;
                &:hover {
                  color: #c7b299;
                }
              }
            }
          }
          .text {
            transition: opacity 2s;
            padding: 20px 0;
            background: #fbfaf8;
            padding-left: 22px;
            .md(padding-left, 12px);
            h6 {
              color: #c7b299;
              font-size: 20px;
              font-weight: 500;
              font-family: 'Raleway';
            }
            p {
              font-size: 14px;
              color: #d1d1d1;
            }
          }
        }
      }
    }
  }
}