header {
  position: relative;
  .carousel {
    position: relative;
    .carousel-indicators{
        li{
            width: 10px;
            height: 10px;
           border-radius: 50%;
           &.active{
               border: 1px solid #d3c1ad;
               background: transparent;
           }
        }
    }
    .carousel-inner{
        .carousel-item{
            .carousel-caption{
                bottom: 33%;
                .lg(bottom, 20%);
                text-align: center;
                h5{
                   font-family: 'Raleway';
                   font-weight: bold;
                   font-size: 36px;
                   color: #fff; 
                }
                p{
                    font-size: 16px;
                    color: #fff;  
                    max-width: 570px;
                    margin: auto;
                }
                .btn{
                    margin-top: 30px;
                    background: #998675;
                    color: #fff;
                    padding: 10px 30px;
                    border-bottom: 2px solid #736357;
                    font-family: 'Raleway';
                    font-weight: bolder;
                    font-size: 14px;
                }
            }
        }
    }
  }
}
