.profit {
  padding: 65px 0;
  .container {
    padding: 0;
    .row {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      .item {
        text-align: center;
        margin: 15px;
        flex-basis: 265px;
        .xl(flex-basis, 215px);
        &:nth-child(1) {
          margin-left: 0;
          .xl(margin-left, 15px);
        }
        &:nth-child(4) {
          margin-right: 0;
          .xl(margin-right, 15px);
        }
        .subtitle {
          font-family: 'Raleway';
          font-weight: bolder;
          font-size: 22px;
          color: #555;
        }
        .text {
          color: #8c8c8c;
          font-family: 'Montserrat';
          font-weight: lighter;
          font-size: 14px;
        }
      }
    }
  }
}