.waxom {
  background: #fbfaf8;
  padding-top: 70px;
  h3 {
    font-size: 30px;
  }
  p {
    font-size: 16px;
    margin-bottom: 40px;
  }
  .multipurpose {
    background: url('../img/multipurposeBg.png') center center;
    padding: 40px 0;
    .text {
      .sm(text-align,
      center);
      h4 {
        span {
          color: #c7b299;
        }
        color: #fff;
        font-family: 'Raleway';
        font-size: 28px;
        font-weight: 400;
        text-transform: capitalize;
      }
      p {
        font-size: 18px;
        color: #615855;
        margin: 0 !important;
        .md(margin-bottom,
        1rem);
      }
    }
    .btn {
      align-self: center;
      background: #998675;
      color: #fff;
      padding: 10px 30px;
      border-bottom: 2px solid #736357;
      font-family: 'Raleway';
      font-weight: bolder;
      font-size: 14px;
      .md(margin,
      auto);
    }
  }
}