footer{
    padding: 80px 0;background: #191919;
 .row{
    justify-content: space-evenly;
     .sm-block({
        flex-direction: column;
     });
      .item{
          .sm(align-self, center);
     margin-top: 35px;
     padding: 0;
     width: 270px !important;
     
    h3{
        color: #fff;
        font-size: 24px;
        margin-bottom: 25px;
    }
    .btn{
        background: none;
        border:none;
        color: #fff;
        font-size: 14px;
        font-weight: 400;
        padding: 0;
       
    }
    p{
        max-width: 265px;
        font-size: 14px;
        .date{
            font-size: 12px;
        }
        .twitter{
            color: #00bff3;
        }
    }
    hr{
        color: #252525;
        height: 1px;
        max-width: 265px;
    }
    .widgets{
        display: flex;
        flex-wrap: wrap;
        .link{
            flex-basis: 130px;
            &:nth-child(1){
               margin-right: 10px;
               margin-bottom: 10px;
            }
            &:nth-child(4){
               margin-left: 10px;
               
            }
        }
    }
 }
 }
   
 
}