.mobile {
  padding-top: 70px;
  .excellent {
    .lg(margin-top, 0);
    .md(margin-top, 50px);
    margin-top: 120px;
    .lg(margin-bottom, 20px);
    margin-bottom: 33px;
    text-align: left;
    h3 {
      font-size: 30px;
    }
    p {
      font-size: 16px;
    }
  }
  .nav {
    padding-left: 20px;
    .lg(padding-left, 10px);
    li {
      img {
        margin-right: 12px;
        .lg(margin-right, 8px);
      }
      margin: 5px;
      font-family: 'Montserrat';
      font-weight: lighter;
      color: #8c8c8c;
      font-size: 16px;
      .lg(font-size, 10px);
      .md(font-size, 12px);
    }
  }
}