.info{
    background: url('../img/multipurposeBg.png');
   padding: 60px 0;
    .nav{
         display: flex;
         flex-wrap: wrap;
         justify-content: space-evenly;
         .item{
            flex-basis: 150px; 
            
            text-align: center;
            img{
            }
            .num{
               font-size: 48px;
               color: #fff; 
               font-weight: 300;
               margin-top: 25px;
               margin-bottom: 18px;
            }
            h5{
               color: #c7b299; 
               text-transform: uppercase;
               font-size: 14px;
               position: relative;
               &::after{
                   content: "";
                   position: absolute;
                   width: 30px;
                   height: 2px;
                   background: #fff;
                   display: flex;
                   right: 60px;
                   top: 35px;
               }
            }
         }
    }
}